.loader {
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.loader span {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 5px dotted #333;
  border-top: 5px dotted transparent;
  display: block;
  animation: loader 2s forwards infinite;
}

@keyframes loader {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}/*# sourceMappingURL=PreLoader.css.map */